import React from "react";
import "bootstrap/dist/css/bootstrap.min.css";
import styles from "./About.module.css";
import Tilt from "react-parallax-tilt";
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import {faGithub, faLinkedinIn} from "@fortawesome/free-brands-svg-icons";
import rust from "../../assets/rust.png"

const About = () => {
    return (
        <div className={`container-fluid ${styles[`about-section`]}`} id="about">
            <div className={`container`}>
                <div className={`row`}>
                    <div className={`col-md-8 ${styles[`about-description`]}`}>
                        <h1 style={{fontSize: "2.6em"}}>
                            LET ME <span className="purple"> INTRODUCE </span> MYSELF
                        </h1>
                        <p className={`${styles["about-body"]}`}>
                            I fell in love with programming and I have at least learnt
                            something, I think… 🤷‍♂️
                            <br/>
                            <br/>I am fluent in
                            <i>
                                <b className="purple"> Java. </b>
                            </i>
                            And a tiny bit in
                            <b className="purple"> JavaScript. </b>
                            <br/>
                            <br/>
                            My current focus is on &nbsp;
                            <i>
                                <b className="purple">
                                    Reactive Programming and Rust.
                                </b>
                            </i>
                            <br/>
                            <br/>
                            Whenever possible, I also apply my passion for developing products
                            with
                            <i>
                                <b className="purple"> Modern Java Frameworks and toolkits</b>
                            </i>
                            &nbsp; like
                            <i>
                                <b className="purple"> Spring Boot Webflux and Vert.x.</b>
                            </i>
                        </p>
                    </div>
                    <div className={`col-md-4 ${styles[`my-avatar`]}`}>
                        <Tilt>
                            <img src={rust} className="img-fluid" alt="avatar"/>
                        </Tilt>
                    </div>
                </div>
                <div className={`row`}>
                    <div className={`col-md-12 ${styles[`about-social`]}`}>
                        <h1>FIND ME ON</h1>
                        <p>
                            Feel free to <span className="purple">connect </span>with me
                        </p>
                        <ul className={`${styles[`about-social-links`]}`}>
                            <li className={`${styles[`social-icons`]}`}>
                                <a
                                    href="https://github.com/pan-daniel"
                                    target="_blank"
                                    rel="noreferrer"
                                    className={`${styles[`awesome-social-icons`]} ${styles[`icon-colour`]}`}
                                >
                                    <FontAwesomeIcon icon={faGithub}/>
                                </a>
                            </li>
                            <li className={`${styles[`social-icons`]}`}>
                                <a
                                    href="https://www.linkedin.com/in/daniel-grzelak/"
                                    target="_blank"
                                    rel="noreferrer"
                                    className={`${styles[`awesome-social-icons`]} ${styles[`icon-colour`]}`}
                                >
                                    <FontAwesomeIcon icon={faLinkedinIn}/>
                                </a>
                            </li>
                        </ul>
                    </div>
                </div>
            </div>
        </div>
    );
}
export default About;