import React, {useState} from 'react';
import {FontAwesomeIcon} from '@fortawesome/react-fontawesome';
import {faCodeBranch, faHome} from "@fortawesome/free-solid-svg-icons";
import {faStar} from "@fortawesome/free-regular-svg-icons";
import logo from '../../assets/logo.png';
import styles from './Navigation.module.css';
import 'bootstrap/dist/css/bootstrap.css';

const Navigation: React.FC = () => {
    const [navColour, updateNavbar] = useState(false);

    function scrollHandler() {
        if (window.scrollY >= 20) {
            updateNavbar(true);
        } else {
            updateNavbar(false);
        }
    }

    window.addEventListener("scroll", scrollHandler);

    const [isNavCollapsed, setIsNavCollapsed] = useState(true);

    const handleNavCollapse = () => setIsNavCollapsed(!isNavCollapsed);

    return (
        <nav
            className={"navbar navbar-expand-md fixed-top  " + `${navColour ? styles.sticky : styles.navbar}`}
        >
            <div className={"container"}>
                <a className={`navbar-brand ${styles.brand}`} href="#">
                    <img src={logo} className={"img-fluid logo " + styles.logo} alt="brand"/>
                </a>
                <button className={`navbar-toggler ${styles.toggler} collapsed`}
                        type="button"
                        data-bs-toggle="collapse"
                        data-bs-target="#responsive-navbar-nav"
                        aria-controls="responsive-navbar-nav"
                        aria-expanded={!isNavCollapsed}
                        aria-label="Toggle navigation"
                        onClick={handleNavCollapse}>
                    <span/>
                    <span/>
                    <span/>
                </button>

                <div id="responsive-navbar-nav" className={`${isNavCollapsed ? 'collapse' : ''} navbar-collapse`}>
                    <ul className={"navbar-nav ml-auto " + styles.nav}>
                        <li className={"nav-item " + styles.item}>
                            <a className={"nav-link active " + styles.link} aria-current="page" href="#">
                                <FontAwesomeIcon icon={faHome}/>
                                {` `}
                                Home
                            </a>
                        </li>
                        <li className={"nav-item " + styles.fork}>
                            <a className={"btn btn-primary " + styles[`fork-inner`]}
                               href="https://github.com/pan-daniel/pan-daniel.github.io">
                                <FontAwesomeIcon icon={faCodeBranch}/>
                                <FontAwesomeIcon icon={faStar}/>
                            </a>
                        </li>
                    </ul>
                </div>
            </div>
        </nav>
    );
}

export default Navigation;
